.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 30px;
  z-index: 1;
  display: flex;
  justify-content: space-between;

  /* Animation  */
  transition-timing-function: ease-in;
  transition: all 1s;

}
.nav__black{
  background-color: #111;
}

.nav__logo {

  position: fixed;
  left: 20;
  width: 40px;
  object-fit: contain;
}

img.nav__avatar {
  position: fixed;
  right: 20px;
  width: 40px;
  object-fit: contain;
}
