.row {
    margin: 20px;
}

.row__posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
    padding: 20px;
}
.row__posters::-webkit-scrollbar{
    display: none;
}

.row__poster {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}
.row__posterLarge{
    max-height: 250px;
}
.row__poster:hover{
    transform: scale(1.08);
    cursor: pointer;
}